import React, { useEffect, useState } from "react";
import { Select, MenuItem } from "@mui/material";
import { useTheme } from "@mui/material/styles";
import DashboardCard from "../../../components/shared/DashboardCard";
import Chart from "react-apexcharts";
import axios from "axios";

const SalesOverview = () => {
  const [monthlyRevenue, setMonthlyRevenue] = useState([]); // D·ªØ li·ªáu doanh thu theo th√°ng
  const [year, setYear] = useState(2024);

  const theme = useTheme();
  const primary = theme.palette.primary.main;

  // G·ªçi API ƒë·ªÉ l·∫•y doanh thu cho nƒÉm ƒë∆∞·ª£c ch·ªçn
  useEffect(() => {
    fetchMonthlyRevenue(year);
  }, [year]);

  // const fetchMonthlyRevenue = async (selectedYear) => {
  //   try {
  //     const response = await axios.get(
  //       `http://localhost:8080/api/getDoanhThuChart?year=${selectedYear}`
  //     );
  //     setMonthlyRevenue(response.data || []); // Gi·∫£ s·ª≠ API tr·∫£ v·ªÅ m·∫£ng doanh thu theo th√°ng
  //   } catch (error) {
  //     console.error("L·ªói khi l·∫•y doanh thu: ", error);
  //     setMonthlyRevenue([]);
  //   }
  // };
  const fetchMonthlyRevenue = async (selectedYear) => {
    try {
      const response = await axios.get(
        `http://localhost:8080/api/getDoanhThuChart?year=${selectedYear}`
      );

      console.log("D·ªØ li·ªáu t·ª´ API:", response.data);

      // M·∫£ng m·∫∑c ƒë·ªãnh cho 12 th√°ng, gi√° tr·ªã ban ƒë·∫ßu l√† 0
      const defaultRevenue = Array(12).fill(0);

      // Gi·∫£ s·ª≠ d·ªØ li·ªáu API tr·∫£ v·ªÅ d·∫°ng [{ month: 1, revenue: 100 }, ...]
      if (Array.isArray(response.data)) {
        response.data.forEach((item) => {
          if (item.month >= 1 && item.month <= 12) {
            defaultRevenue[item.month - 1] = item.revenue || 0; // ƒê·∫∑t doanh thu v√†o ƒë√∫ng th√°ng
          }
        });
      }

      setMonthlyRevenue(defaultRevenue); // Ghi nh·∫≠n d·ªØ li·ªáu ƒë√£ x·ª≠ l√Ω
    } catch (error) {
      console.error("L·ªói khi l·∫•y doanh thu: ", error.response || error);
      setMonthlyRevenue(Array(12).fill(0)); // Gi√° tr·ªã m·∫∑c ƒë·ªãnh khi l·ªói
    }
  };

  const handleYearChange = (event) => {
    const selectedYear = event.target.value;
    console.log("NƒÉm ƒë∆∞·ª£c ch·ªçn:", selectedYear);
    setYear(selectedYear);
  };

  // Thay ƒë·ªïi d·ªØ li·ªáu cho chart theo doanh thu ƒë√£ l·∫•y
  const seriescolumnchart = [{ name: "Doanh thu", data: monthlyRevenue }];

  const currentYear = new Date().getFullYear();

  return (
    <DashboardCard
      title={`Doanh thu trong nƒÉm ${currentYear}`}
      action={
        <Select
          id="year-dd"
          value={year}
          size="small"
          onChange={handleYearChange}
        >
          {[2022, 2023, 2024, 2025].map((yearOption) => (
            <MenuItem key={yearOption} value={yearOption}>
              NƒÉm {yearOption}
            </MenuItem>
          ))}
        </Select>
      }
    >
      <Chart
        options={{
          chart: {
            type: "bar",
            fontFamily: "'Plus Jakarta Sans', sans-serif",
            foreColor: "#adb0bb",
            toolbar: { show: true },
            height: 370,
          },
          colors: [primary],
          plotOptions: {
            bar: {
              horizontal: false,
              columnWidth: "42%",
              borderRadius: 6,
            },
          },
          stroke: { show: true, width: 5, colors: ["transparent"] },
          dataLabels: { enabled: false },
          grid: { borderColor: "rgba(0,0,0,0.1)", strokeDashArray: 3 },
          yaxis: { tickAmount: 4 },
          xaxis: {
            categories: [
              "January",
              "February",
              "March",
              "April",
              "May",
              "June",
              "July",
              "August",
              "September",
              "October",
              "November",
              "December",
            ],
          },
          tooltip: {
            theme: theme.palette.mode === "dark" ? "dark" : "light",
          },
        }}
        series={seriescolumnchart}
        type="bar"
        height="370px"
      />
    </DashboardCard>
  );
};

export default SalesOverview;
