import React, { useEffect, useState } from "react";
import Chart from "react-apexcharts";
import axios from "axios";
import DashboardCard from "../../../components/shared/DashboardCard";
import { Select } from "antd"; // Import Select t·ª´ Ant Design

const { Option } = Select;

const TrangThaiDonHang = () => {
  const [statusData, setStatusData] = useState({}); // D·ªØ li·ªáu tr·∫°ng th√°i ƒë∆°n h√†ng
  //const [month, setMonth] = useState(new Date().getMonth() + 1); // Th√°ng hi·ªán t·∫°i
  //const [year, setYear] = useState(new Date().getFullYear()); // NƒÉm hi·ªán t·∫°i
  const [month, setMonth] = useState(9); // M·∫∑c ƒë·ªãnh th√°ng 9
  const [year, setYear] = useState(2024);
  // G·ªçi API ƒë·ªÉ l·∫•y tr·∫°ng th√°i ƒë∆°n h√†ng
  useEffect(() => {
    fetchOrderStatus();
  }, [month, year]);

  const fetchOrderStatus = async () => {
    try {
      const response = await axios.get(
        `http://localhost:8080/api/getTrangThaiDonHang?month=${month}&year=${year}`
      );
      setStatusData(response.data || {});
    } catch (error) {
      console.error("L·ªói khi l·∫•y tr·∫°ng th√°i ƒë∆°n h√†ng: ", error);
      setStatusData({});
    }
  };

  // Chuy·ªÉn ƒë·ªïi d·ªØ li·ªáu cho bi·ªÉu ƒë·ªì
  const chartData = {
    labels: Object.keys(statusData), // Tr·∫°ng th√°i ƒë∆°n h√†ng
    series: Object.values(statusData), // S·ªë l∆∞·ª£ng ƒë∆°n h√†ng cho t·ª´ng tr·∫°ng th√°i
  };

  return (
    <DashboardCard title={`ƒê∆°n h√†ng th√°ng ${month} nƒÉm ${year}`}>
      <div style={{ display: "flex", gap: "20px", marginBottom: "20px" }}>
        <Select
          value={month}
          onChange={(value) => setMonth(value)}
          style={{ width: 120 }}
          placeholder="Ch·ªçn th√°ng"
        >
          {[...Array(12).keys()].map((m) => (
            <Option key={m} value={m + 1}>
              {m + 1}
            </Option>
          ))}
        </Select>

        <Select
          value={year}
          onChange={(value) => setYear(value)}
          style={{ width: 120 }}
          placeholder="Ch·ªçn nƒÉm"
        >
          {[2020, 2021, 2022, 2023, 2024].map((y) => (
            <Option key={y} value={y}>
              {y}
            </Option>
          ))}
        </Select>
      </div>
      <Chart
        options={{
          labels: chartData.labels,
          colors: ["#008FFB", "#00E396", "#FEB019", "#FF4560", "#775DD0"],
          plotOptions: {
            pie: {
              donut: {
                size: "60%",
              },
            },
          },
          tooltip: {
            theme: "light",
          },
        }}
        series={chartData.series}
        type="donut"
        height="370px"
      />
    </DashboardCard>
  );
};

export default TrangThaiDonHang;
